// src/components/Hero.js
// src/components/Hero.js

import React, { useMemo } from "react";
import { motion } from "framer-motion";
import { ArrowDown, Download, Github, Linkedin, Mail } from "lucide-react";
import { usePortfolio } from "../contexts/PortfolioContext";
import { Button } from "./ui/button";

const flattenSkills = (skillsObj) => {
  if (!skillsObj || typeof skillsObj !== "object") return [];
  const flat = [];
  for (const items of Object.values(skillsObj)) {
    if (Array.isArray(items)) flat.push(...items);
  }
  const seen = new Set();
  return flat.filter((label) => {
    const k = String(label || "").trim().toLowerCase();
    if (!k || seen.has(k)) return false;
    seen.add(k);
    return true;
  });
};

const Hero = () => {
  const { portfolio, loading } = usePortfolio();
  const personalInfo = portfolio?.personal_info || {};

  const name = personalInfo.name ?? "Your Name";
  const title = personalInfo.title ?? "";
  const tagline = personalInfo.tagline ?? "";
  const resumeUrl = personalInfo.resumeUrl ?? "souhardabhattacharjee.pdf";
  const github = personalInfo.github ?? "";
  const linkedin = personalInfo.linkedin ?? "";
  const email = personalInfo.email ?? "";
  const profileImage = personalInfo.profileImage || "/images/souharda.png";

  // ✅ Ensure correct public path (works on GitHub Pages too)
  const imageSrc = `${process.env.PUBLIC_URL}${profileImage}`;
  const resumeHref = `${process.env.PUBLIC_URL}/${resumeUrl}`;

  const skills = useMemo(() => flattenSkills(portfolio?.skills), [portfolio]);

  if (loading) {
    return (
      <section id="home" className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-gray-300 dark:border-gray-700 border-t-black dark:border-t-white rounded-full animate-spin mx-auto" />
          <p className="mt-4 text-gray-600 dark:text-gray-400">Loading...</p>
        </div>
      </section>
    );
  }

  const scrollToSection = (id) => {
    const el = document.querySelector(id);
    if (el) el.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <section
      id="home"
      className="min-h-screen flex items-center justify-center relative overflow-hidden"
    >
      {/* Background gradient */}
      <div className="absolute inset-0 bg-gradient-to-br from-gray-50 to-gray-100 dark:from-black dark:to-gray-900" />

      {/* Blurred shapes */}
      <div className="absolute inset-0 opacity-10">
        <div className="absolute top-20 left-10 w-72 h-72 bg-gray-400 dark:bg-gray-700 rounded-full filter blur-3xl" />
        <div className="absolute bottom-20 right-10 w-96 h-96 bg-gray-300 dark:bg-gray-800 rounded-full filter blur-3xl" />
      </div>

      {/* Main content */}
      <div className="container mx-auto px-4 relative z-10">
        <div className="flex flex-col md:flex-row items-center justify-center gap-12 max-w-6xl mx-auto text-center md:text-left">

          {/* LEFT: Profile Image */}
          <motion.div
            initial={{ opacity: 0, x: -40 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8 }}
            className="flex justify-center md:justify-end"
          >
            <img
              src={imageSrc}
              alt={name}
              className="w-64 h-64 md:w-80 md:h-80 rounded-full object-cover shadow-2xl border-4 border-gray-700 dark:border-gray-600"
              onError={(e) => (e.target.src = `${process.env.PUBLIC_URL}/images/souharda.png`)}
            />
          </motion.div>

          {/* RIGHT: Text and Buttons */}
          <motion.div
            initial={{ opacity: 0, x: 40 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8 }}
            className="max-w-2xl"
          >
            <motion.h1
              className="text-5xl md:text-6xl font-bold text-black dark:text-white mb-4 whitespace-nowrap leading-tight tracking-tight"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.3 }}
            >
              {name}
            </motion.h1>

            <motion.h2
              className="text-2xl md:text-3xl text-gray-600 dark:text-gray-400 mb-3"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.4 }}
            >
              {title}
            </motion.h2>

            <motion.p
              className="text-lg md:text-xl text-gray-500 dark:text-gray-500 mb-8 leading-relaxed"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.6 }}
            >
              {tagline}
            </motion.p>

            {/* Buttons */}
            <motion.div
              className="flex flex-wrap justify-center md:justify-start gap-4 mb-8"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.8 }}
            >
              <Button
                size="lg"
                className="bg-black dark:bg-white text-white dark:text-black hover:bg-gray-800 dark:hover:bg-gray-200 transition-all"
                onClick={() => scrollToSection("#contact")}
              >
                <Mail className="mr-2 h-5 w-5" />
                Get In Touch
              </Button>

              <Button
                asChild
                size="lg"
                variant="outline"
                className="border-2 border-black dark:border-white text-black dark:text-white hover:bg-black hover:text-white dark:hover:bg-white dark:hover:text-black transition-all"
              >
                <a href={resumeHref} download>
                  <span className="inline-flex items-center">
                    <Download className="mr-2 h-5 w-5" />
                    Download CV
                  </span>
                </a>
              </Button>
            </motion.div>

            {/* Socials */}
            <motion.div
              className="flex justify-center md:justify-start gap-6 mb-6"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 1 }}
            >
              {github && (
                <motion.a
                  href={github}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-gray-600 dark:text-gray-400 hover:text-black dark:hover:text-white transition-colors"
                  whileHover={{ scale: 1.2, rotate: 5 }}
                >
                  <Github className="h-6 w-6" />
                </motion.a>
              )}
              {linkedin && (
                <motion.a
                  href={linkedin}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-gray-600 dark:text-gray-400 hover:text-black dark:hover:text-white transition-colors"
                  whileHover={{ scale: 1.2, rotate: -5 }}
                >
                  <Linkedin className="h-6 w-6" />
                </motion.a>
              )}
              {email && (
                <motion.a
                  href={`mailto:${email}`}
                  className="text-gray-600 dark:text-gray-400 hover:text-black dark:hover:text-white transition-colors"
                  whileHover={{ scale: 1.2, rotate: 5 }}
                >
                  <Mail className="h-6 w-6" />
                </motion.a>
              )}
            </motion.div>
          </motion.div>
        </div>
      </div>

      {/* Scroll-down arrow */}
      <motion.div
        className="absolute bottom-8 left-1/2 transform -translate-x-1/2"
        animate={{ y: [0, 10, 0] }}
        transition={{ repeat: Infinity, duration: 8, ease: "easeInOut" }}
      >
        <button
          onClick={() => scrollToSection("#about")}
          className="text-gray-600 dark:text-gray-400 hover:text-black dark:hover:text-white transition-colors"
        >
          <ArrowDown className="h-6 w-6" />
        </button>
      </motion.div>
    </section>
  );
};

export default Hero;
